body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

button {
  margin-right: 10px;
}

h1 {
  margin: 10px;
}

.toggleButtonRow {
  margin: 20px 10px;
}

.chartContainer {
  float: left;
  margin: 20px;
  width: 300px;
}

.toggle {
  display: inline-block;
  padding: 5px 15px;
  margin-left: 10px;
  border: 1px solid transparent;
  border-radius: 10px;
  cursor: pointer;
  user-select: none;
}

.toggle.-chart-type {
  background: rgb(82, 204, 218);
  color: white;
}

.toggle.-active {
  background: rgb(82, 204, 218);
  color: white;
}

.toggle.-inactive {
  border: 1px solid rgb(82, 204, 218);
} 
